.gaveta{
    margin: 16px 16px;
    color: black;
    width: 923px;
    height: fit-content;
    box-shadow: 75px 89px 33px rgba(0, 0, 0, 0), 48px 57px 30px rgba(0, 0, 0, 0.02), 27px 32px 25px rgba(0, 0, 0, 0.06), 12px 14px 19px rgba(0, 0, 0, 0.1), 3px 4px 10px rgba(0, 0, 0, 0.12);
    background-color: white;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 0px 16px 24px 32px;
}

.card{
    width: 100%;
    padding-top: 32px;
    padding-bottom: 16px;
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    align-items: center;
    flex-shrink: 0;
    border-radius: 20px;
    color: black;
    background-color: white;
    overflow: hidden;
}

.simbolo-nova-notificacao{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    position: absolute;
    z-index: 1;
    background-color: #F2B705;
    margin-bottom: 80px;
    margin-left: -35px;
}

.conteudo {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: hidden; /* Garantir que o conteúdo interno não vaze */
    text-overflow: ellipsis;
    width: 95%;
  }

.titulo-notificacao{
    font-family: Quicksand;
    font-size: 22.93px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-right: 8px;
    min-width: fit-content;
    max-width: 800px;
    overflow: hidden;
    text-overflow: ellipsis;
    flex-shrink: 1;
    flex-grow: 1;
}

.corpo-notificacao{
    font-family: Quicksand;
    font-size: 22.93px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-right: 16px;
    flex-grow: 1;
    flex-shrink: 1;
}

.icone-expandir{
    margin: 0px 16px;
    height: 15px;
    max-width: 100%;
    flex-shrink: 0;
}
