.fundoPag {
    width: 100%;
    height: 100vh;
    background-image: url(../../utils/assets/solucao.png);
    background-repeat: no-repeat;
    background-size: 100% auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.containner_login {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.blocoLogin {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    background: #F2F2F2;
    box-shadow: 48px 163px 48px 0px rgba(0, 0, 0, 0.00), 31px 104px 43px 0px rgba(0, 0, 0, 0.01), 17px 59px 37px 0px rgba(0, 0, 0, 0.05), 8px 26px 27px 0px rgba(0, 0, 0, 0.09), 2px 7px 15px 0px rgba(0, 0, 0, 0.10);
    height: 67vh;
    width: 44%;
    border-radius: 30px;
}

.tituloBloco {
    color: #025373;
    font-weight: 800;
    font-size: 2.5rem;
}

.blocoErro {
    background-color: #9F1E1E;
    width: 100%;
    height: 8vh;
    display: none;
    justify-content: center;
    align-items: center;
    font-weight: 600;
}

.inputsBloco {
    display: flex;
    flex-direction: column;
    width: 85%;
    height: 40vh;
    justify-content: space-around;
}

.inputEmail {
    display: flex;
    flex-direction: column;
    height: 12vh;
    justify-content: space-between;
}

.inputEmail label {
    color: #025373;
    font-weight: 700;
    font-size: 1.3rem;
}

.inputLoginEmail {
    height: 6vh;
    border-radius: 5px;
    border: none;
    font-size: 1rem;
    padding-left: 3%;
    background: #E2E2E2;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset;
}

input{
    all: unset;
    color: black;
}

.inputSenha {
    display: flex;
    flex-direction: column;
    height: 15vh;
    width: 100%;
    justify-content: space-between;
}

.inputSenha label {
    color: #025373;
    font-weight: 700;
    font-size: 1.3rem;
}

.inputSenha img{
    cursor: pointer;
} 

.inputLoginSenha {
    height: 6vh;
    border-radius: 5px;
    border: none;
    font-size: 1rem;
    padding-left: 2%;
    width: 85%;
}   

.input{
    display: flex;
    background: #E2E2E2;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset;
    width: 100%;
    height: 6vh;
    justify-content: center;
    border-radius: 5px;
}

.divImg{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 6vh;
    width: 10%;
}

input{
    all: unset;
    color: black;
}

.msgEsquecerSenha{
    color: black;
    font-size: smaller;
    font-weight: 500;
    display: flex;
    width: 100%;
    flex-direction: row-reverse;
    margin-top: 1%;
    cursor: pointer;
}

.btLogin{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btLogin button{
    border: none;
    border-radius: 20px;
    background-color: #025373;
    height: 8vh;
    color: #F2F2F2;
    font-weight: 600;
    font-size: 20px;
    padding: 0 8% 0 8%;
    cursor: pointer;
}